<template>
  <view class="container">
    <custom-nav-bar title="ËÆ¢ÂçïËØ¶ÊÉÖ" :show-back="true"></custom-nav-bar>
    
    <!-- Âä†ËΩΩ‰∏≠Áä∂ÊÄÅ -->
    <view v-if="isLoading || loading" class="loading-container">
      <view class="loading-spinner"></view>
      <text class="loading-text">Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ‰∏≠...</text>
    </view>
    
    <!-- ËÆ¢Âçï‰∏çÂ≠òÂú®Áä∂ÊÄÅ -->
    <view v-else-if="!orderInfo" class="empty-container">
      <image class="empty-icon" src="/static/images/empty-order.png" mode="aspectFit"></image>
      <text class="empty-title">Êú™ÊâæÂà∞ËÆ¢Âçï‰ø°ÊÅØ</text>
      <view class="empty-desc">ËÆ¢ÂçïID: {{id}}</view>
      <button class="primary-btn" @click="goBack">ËøîÂõû</button>
    </view>
    
    <!-- ËÆ¢ÂçïËØ¶ÊÉÖÂÜÖÂÆπ -->
    <block v-else>
      <scroll-view scroll-y class="order-scroll">
        <!-- ËÆ¢ÂçïÁä∂ÊÄÅÂç°Áâá -->
        <view class="status-card">
          <view class="status-icon" :class="getStatusClass(orderInfo.status)">
            <text class="status-emoji">{{getStatusEmoji(orderInfo.status)}}</text>
          </view>
          <view class="status-info">
            <text class="status-text">{{getStatusText(orderInfo.status)}}</text>
            <text class="status-desc">{{getStatusDescription(orderInfo.status)}}</text>
          </view>
        </view>
        
        <!-- ËÆ¢Âçï‰ø°ÊÅØÂç°Áâá -->
        <view class="order-card">
          <view class="section-title">ËÆ¢Âçï‰ø°ÊÅØ</view>
          <view class="info-row">
            <text class="info-label">ËÆ¢ÂçïÁºñÂè∑</text>
            <view class="info-value-copy">
              <text class="info-value">{{orderInfo.id}}</text>
              <text class="copy-btn" @click="copyOrderId">Â§çÂà∂</text>
            </view>
          </view>
          <view class="info-row">
            <text class="info-label">‰∏ãÂçïÊó∂Èó¥</text>
            <text class="info-value">{{formatTime(orderInfo.createTime)}}</text>
          </view>
          <view class="info-row" v-if="orderInfo.payTime">
            <text class="info-label">ÊîØ‰ªòÊó∂Èó¥</text>
            <text class="info-value">{{formatTime(orderInfo.payTime)}}</text>
          </view>
          <view class="info-row" v-if="orderInfo.cancelTime">
            <text class="info-label">ÂèñÊ∂àÊó∂Èó¥</text>
            <text class="info-value">{{formatTime(orderInfo.cancelTime)}}</text>
          </view>
          <view class="info-row">
            <text class="info-label">ÊîØ‰ªòÊñπÂºè</text>
            <text class="info-value">ÂæÆ‰ø°ÊîØ‰ªò</text>
          </view>
        </view>
        
        <!-- ÂïÜÂìÅÂàóË°®Âç°Áâá -->
        <view class="order-card">
          <view class="section-title">ÂïÜÂìÅ‰ø°ÊÅØ</view>
          <view class="product-list">
            <view v-for="(item, index) in orderItems" :key="index" class="product-item">
              <image class="product-image" :src="getProductImage(item)" mode="aspectFill"></image>
              <view class="product-info">
                <text class="product-name">{{item.productName}}</text>
                <view class="product-price-row">
                  <text class="product-price">¬•{{item.price}}</text>
                  <text class="product-quantity">x{{item.quantity}}</text>
                </view>
              </view>
            </view>
          </view>
          
          <!-- ‰ª∑Ê†ºÊòéÁªÜ -->
          <view class="price-summary">
            <view class="price-row">
              <text>ÂïÜÂìÅÊÄªÈ¢ù</text>
              <text>¬•{{orderInfo.totalAmount}}</text>
            </view>
            <view class="price-row" v-if="orderInfo.totalAmount !== orderInfo.payAmount">
              <text>‰ºòÊÉ†ÈáëÈ¢ù</text>
              <text class="discount-price">-¬•{{orderInfo.totalAmount - orderInfo.payAmount}}</text>
            </view>
            <view class="price-row total">
              <text>ÂÆû‰ªòÈáëÈ¢ù</text>
              <text class="total-price">¬•{{orderInfo.payAmount}}</text>
            </view>
          </view>
        </view>
        
        <!-- Êî∂Ë¥ß‰ø°ÊÅØÂç°Áâá -->
        <view class="order-card" v-if="orderInfo.address || orderInfo.phone">
          <view class="section-title">Êî∂Ë¥ß‰ø°ÊÅØ</view>
          <view class="info-row" v-if="orderInfo.address">
            <text class="info-label">Êî∂Ë¥ßÂú∞ÂùÄ</text>
            <text class="info-value address">{{orderInfo.address}}</text>
          </view>
          <view class="info-row" v-if="orderInfo.phone">
            <text class="info-label">ËÅîÁ≥ªÁîµËØù</text>
            <text class="info-value">{{orderInfo.phone}}</text>
          </view>
        </view>
        
        <!-- Â∫óÈì∫‰ø°ÊÅØÂç°Áâá -->
        <view class="order-card">
          <view class="section-title">Â∫óÈì∫‰ø°ÊÅØ</view>
          <view class="info-row">
            <text class="info-label">Â∫óÈì∫ID</text>
            <text class="info-value">{{orderInfo.shopId}}</text>
          </view>
          <button class="shop-btn" @click="goToShop">Êü•ÁúãÂ∫óÈì∫</button>
        </view>
      </scroll-view>
      
      <!-- Â∫ïÈÉ®Êìç‰ΩúÊåâÈíÆ -->
      <view class="bottom-actions" v-if="orderInfo.status !== 3">
        <button v-if="orderInfo.status === 0" class="action-btn cancel" @click="cancelOrder">ÂèñÊ∂àËÆ¢Âçï</button>
        <button v-if="orderInfo.status === 0" class="action-btn primary" @click="payOrder">Á´ãÂç≥ÊîØ‰ªò</button>
        <button v-if="orderInfo.status === 1" class="action-btn primary" @click="completeOrder">Á°ÆËÆ§Êî∂Ë¥ß</button>
      </view>
    </block>
  </view>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import CustomNavBar from '@/components/CustomNavBar.vue';
import config from '@/config/index.js';

export default {
  components: { CustomNavBar },
  data() { 
    return { 
      id: null, 
      userId: 1,
      orderData: null,
      loading: false 
    }; 
  },
  computed: {
    ...mapState('order', ['currentOrder','isLoading']),
    
    // ‰ºòÂÖà‰ΩøÁî®Áõ¥Êé•APIËØ∑Ê±ÇËé∑ÂèñÁöÑÊï∞ÊçÆ
    orderInfo() {
      // Â¶ÇÊûúÊúâÁõ¥Êé•Ëé∑ÂèñÁöÑÊï∞ÊçÆÔºå‰ºòÂÖà‰ΩøÁî®
      if (this.orderData) {
        return this.orderData.order || this.orderData;
      }
      
      // Â¶ÇÊûúÊúâVuex‰∏≠ÁöÑÊï∞ÊçÆÔºå‰ΩøÁî®VuexÊï∞ÊçÆ
      if (this.currentOrder) {
        return this.currentOrder.order || this.currentOrder;
      }
      
      return null;
    },
    
    // Ëé∑ÂèñËÆ¢ÂçïÂïÜÂìÅÈ°π
    orderItems() {
      if (!this.orderInfo) return [];
      
      // Â§ÑÁêÜÂµåÂ•óÊï∞ÊçÆÁªìÊûÑ
      if (this.orderData && this.orderData.orderItems) {
        return this.orderData.orderItems;
      }
      
      // Â§ÑÁêÜÂÖ∂‰ªñÂèØËÉΩÁöÑÊï∞ÊçÆÁªìÊûÑ
      if (this.orderInfo.items) {
        return this.orderInfo.items;
      }
      
      if (this.orderInfo.orderItems) {
        return this.orderInfo.orderItems;
      }
      
      return [];
    }
  },
  methods: {
    ...mapActions('order', ['getOrderDetail','payOrder','cancelOrder','completeOrder']),
    
    async loadDetail() { 
      console.log('Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ, orderId:', this.id, 'userId:', this.userId);
      
      try {
        // Â∞ùËØï‰ΩøÁî®Vuex action
        await this.getOrderDetail({orderId: this.id, userId: this.userId});
      } catch(error) {
        console.error('VuexÂä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•:', error);
      }
      
      // Êó†ËÆ∫VuexÊòØÂê¶ÊàêÂäüÔºåÈÉΩÂ∞ùËØïÁõ¥Êé•APIË∞ÉÁî®‰ª•Ëé∑ÂèñÊúÄÊñ∞Êï∞ÊçÆ
      this.loadOrderDetailDirect();
    },
    
    // Áõ¥Êé•ÈÄöËøáAPIËé∑ÂèñËÆ¢ÂçïËØ¶ÊÉÖÔºà‰∏çÈÄöËøáVuexÔºâ
    loadOrderDetailDirect() {
      this.loading = true;
      
      uni.request({
        url: `${config.BaseUrl}/api/shop/order/detail`,
        method: 'GET',
        data: {
          orderId: this.id,
          userId: this.userId
        },
        header: {
          'content-type': 'application/json',
          ...config.getAuthHeader()
        },
        success: (res) => {
          console.log('Áõ¥Êé•APIËé∑ÂèñËÆ¢ÂçïËØ¶ÊÉÖÊàêÂäü:', JSON.stringify(res.data));
          if (res.data && res.data.code === 1) {
            this.orderData = res.data.data;
          } else {
            uni.showToast({
              title: res.data?.msg || 'Ëé∑ÂèñËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•',
              icon: 'none'
            });
          }
        },
        fail: (err) => {
          console.error('Áõ¥Êé•APIËé∑ÂèñËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•:', JSON.stringify(err));
          uni.showToast({
            title: 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
            icon: 'none'
          });
        },
        complete: () => {
          this.loading = false;
        }
      });
    },
    
    // Ëé∑ÂèñÂïÜÂìÅÂõæÁâá
    getProductImage(item) {
      // Â¶ÇÊûúÂïÜÂìÅÈ°πÊúâÂõæÁâáÂ±ûÊÄßÔºåÂàô‰ΩøÁî®
      if (item.image) return item.image;
      
      // Âê¶Âàô‰ΩøÁî®ÈªòËÆ§ÂõæÁâá
      return 'https://ykhyyy.oss-cn-beijing.aliyuncs.com/ht.jpg';
    },
    
    // Ëé∑ÂèñËÆ¢ÂçïÁä∂ÊÄÅÊñáÊú¨
    getStatusText(status) {
      switch(parseInt(status)){
        case 0: return 'ÂæÖÊîØ‰ªò';
        case 1: return 'Â∑≤ÊîØ‰ªò';
        case 2: return 'Â∑≤ÂÆåÊàê';
        case 3: return 'Â∑≤ÂèñÊ∂à';
        default: return 'Êú™Áü•Áä∂ÊÄÅ';
      }
    },
    
    // Ëé∑ÂèñËÆ¢ÂçïÁä∂ÊÄÅÊèèËø∞
    getStatusDescription(status) {
      switch(parseInt(status)){
        case 0: return 'ËØ∑Âú®30ÂàÜÈíüÂÜÖÂÆåÊàêÊîØ‰ªò';
        case 1: return 'ÂïÜÂÆ∂Ê≠£Âú®Â§ÑÁêÜÊÇ®ÁöÑËÆ¢Âçï';
        case 2: return 'ËÆ¢ÂçïÂ∑≤ÂÆåÊàêÔºåÊÑüË∞¢ÊÇ®ÁöÑË¥≠‰π∞';
        case 3: return 'ËÆ¢ÂçïÂ∑≤ÂèñÊ∂à';
        default: return '';
      }
    },
    
    // Ëé∑ÂèñÁä∂ÊÄÅÂØπÂ∫îÁöÑCSSÁ±ª
    getStatusClass(status) {
      switch(parseInt(status)){
        case 0: return 'status-pending';
        case 1: return 'status-paid';
        case 2: return 'status-completed';
        case 3: return 'status-cancelled';
        default: return '';
      }
    },
    
    // Ëé∑ÂèñÁä∂ÊÄÅÂØπÂ∫îÁöÑemoji
    getStatusEmoji(status) {
      switch(parseInt(status)){
        case 0: return '‚è±Ô∏è';
        case 1: return 'üöö';
        case 2: return '‚úÖ';
        case 3: return '‚ùå';
        default: return '‚ùì';
      }
    },
    
    // Â§çÂà∂ËÆ¢ÂçïID
    copyOrderId() {
      if (!this.orderInfo || !this.orderInfo.id) return;
      
      uni.setClipboardData({
        data: this.orderInfo.id.toString(),
        success: () => {
          uni.showToast({
            title: 'ËÆ¢ÂçïÂè∑Â∑≤Â§çÂà∂',
            icon: 'success'
          });
        }
      });
    },
    
    // ÊîØ‰ªòËÆ¢Âçï
    async payOrder() {
      uni.showModal({
        title: 'ËÆ¢ÂçïÊîØ‰ªò',
        content: 'Á°ÆÂÆöË¶ÅÊîØ‰ªòÊ≠§ËÆ¢ÂçïÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            uni.showLoading({
              title: 'Â§ÑÁêÜ‰∏≠...'
            });
            
            try {
              // ‰ΩøÁî®Ê≠£Á°ÆÁöÑAPIË∑ØÂæÑÊ†ºÂºè
              uni.request({
                url: `${config.BaseUrl}/api/shop/order/${this.userId}/${this.id}/pay`,
                method: 'PUT',
                header: {
                  'content-type': 'application/json',
                  ...config.getAuthHeader()
                },
                success: (res) => {
                  uni.hideLoading();
                  if (res.data && res.data.code === 1) {
                    uni.showToast({
                      title: 'ÊîØ‰ªòÊàêÂäü',
                      icon: 'success'
                    });
                    this.loadDetail(); // ÈáçÊñ∞Âä†ËΩΩËÆ¢ÂçïÊï∞ÊçÆ
                  } else {
                    uni.showToast({
                      title: res.data?.msg || 'ÊîØ‰ªòÂ§±Ë¥•',
                      icon: 'none'
                    });
                  }
                },
                fail: (err) => {
                  uni.hideLoading();
                  console.error('ÊîØ‰ªòËÆ¢ÂçïËØ∑Ê±ÇÂ§±Ë¥•:', JSON.stringify(err));
                  uni.showToast({
                    title: 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
                    icon: 'none'
                  });
                }
              });
            } catch(error) {
              uni.hideLoading();
              console.error('ÊîØ‰ªòËÆ¢ÂçïÂ§±Ë¥•:', error);
              uni.showToast({
                title: 'ÊîØ‰ªòÂ§ÑÁêÜÂ§±Ë¥•',
                icon: 'none'
              });
            }
          }
        }
      });
    },
    
    // ÂèñÊ∂àËÆ¢Âçï
    async cancelOrder() {
      uni.showModal({
        title: 'ÂèñÊ∂àËÆ¢Âçï',
        content: 'Á°ÆÂÆöË¶ÅÂèñÊ∂àÊ≠§ËÆ¢ÂçïÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            uni.showLoading({
              title: 'Â§ÑÁêÜ‰∏≠...'
            });
            
            try {
              // ‰ΩøÁî®Ê≠£Á°ÆÁöÑAPIË∑ØÂæÑÊ†ºÂºè
              uni.request({
                url: `${config.BaseUrl}/api/shop/order/${this.userId}/${this.id}/cancel`,
                method: 'PUT',
                header: {
                  'content-type': 'application/json',
                  ...config.getAuthHeader()
                },
                success: (res) => {
                  uni.hideLoading();
                  if (res.data && res.data.code === 1) {
                    uni.showToast({
                      title: 'ËÆ¢ÂçïÂ∑≤ÂèñÊ∂à',
                      icon: 'success'
                    });
                    this.loadDetail(); // ÈáçÊñ∞Âä†ËΩΩËÆ¢ÂçïÊï∞ÊçÆ
                  } else {
                    uni.showToast({
                      title: res.data?.msg || 'ÂèñÊ∂àÂ§±Ë¥•',
                      icon: 'none'
                    });
                  }
                },
                fail: (err) => {
                  uni.hideLoading();
                  console.error('ÂèñÊ∂àËÆ¢ÂçïËØ∑Ê±ÇÂ§±Ë¥•:', JSON.stringify(err));
                  uni.showToast({
                    title: 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
                    icon: 'none'
                  });
                }
              });
            } catch(error) {
              uni.hideLoading();
              console.error('ÂèñÊ∂àËÆ¢ÂçïÂ§±Ë¥•:', error);
              uni.showToast({
                title: 'Â§ÑÁêÜÂ§±Ë¥•',
                icon: 'none'
              });
            }
          }
        }
      });
    },
    
    // ÂÆåÊàêËÆ¢Âçï
    async completeOrder() {
      uni.showModal({
        title: 'Á°ÆËÆ§Êî∂Ë¥ß',
        content: 'Á°ÆËÆ§Â∑≤Êî∂Âà∞ÂïÜÂìÅÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            uni.showLoading({
              title: 'Â§ÑÁêÜ‰∏≠...'
            });
            
            try {
              // ‰ΩøÁî®Ê≠£Á°ÆÁöÑAPIË∑ØÂæÑÊ†ºÂºè
              uni.request({
                url: `${config.BaseUrl}/api/shop/order/${this.userId}/${this.id}/complete`,
                method: 'PUT',
                header: {
                  'content-type': 'application/json',
                  ...config.getAuthHeader()
                },
                success: (res) => {
                  uni.hideLoading();
                  if (res.data && res.data.code === 1) {
                    uni.showToast({
                      title: 'ËÆ¢ÂçïÂ∑≤ÂÆåÊàê',
                      icon: 'success'
                    });
                    this.loadDetail(); // ÈáçÊñ∞Âä†ËΩΩËÆ¢ÂçïÊï∞ÊçÆ
                  } else {
                    uni.showToast({
                      title: res.data?.msg || 'Êìç‰ΩúÂ§±Ë¥•',
                      icon: 'none'
                    });
                  }
                },
                fail: (err) => {
                  uni.hideLoading();
                  console.error('ÂÆåÊàêËÆ¢ÂçïËØ∑Ê±ÇÂ§±Ë¥•:', JSON.stringify(err));
                  uni.showToast({
                    title: 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
                    icon: 'none'
                  });
                }
              });
            } catch(error) {
              uni.hideLoading();
              console.error('ÂÆåÊàêËÆ¢ÂçïÂ§±Ë¥•:', error);
              uni.showToast({
                title: 'Â§ÑÁêÜÂ§±Ë¥•',
                icon: 'none'
              });
            }
          }
        }
      });
    },
    
    // Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(timeStr) {
      if (!timeStr) return 'Êú™Áü•';
      
      try {
        const date = new Date(timeStr);
        return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')} ${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`;
      } catch (e) {
        return timeStr;
      }
    },
    
    // Ë∑≥ËΩ¨Âà∞Â∫óÈì∫È°µÈù¢
    goToShop() {
      if (!this.orderInfo || !this.orderInfo.shopId) return;
      
      uni.navigateTo({
        url: `/pages/mall/shops/detail?id=${this.orderInfo.shopId}`,
        fail: (err) => {
          console.error('ÂØºËà™Âà∞Â∫óÈì∫È°µÈù¢Â§±Ë¥•:', JSON.stringify(err));
          uni.showToast({
            title: 'ÂØºËà™Âà∞Â∫óÈì∫È°µÈù¢Â§±Ë¥•',
            icon: 'none'
          });
        }
      });
    },
    
    // ËøîÂõû‰∏ä‰∏ÄÈ°µ
    goBack() {
      uni.navigateBack({
        delta: 1,
        fail: () => {
          uni.switchTab({
            url: '/pages/mall/index'
          });
        }
      });
    }
  },
  onLoad(options) { 
    this.id = options.id;
    // ‰ªéÊú¨Âú∞Â≠òÂÇ®ÊàñÂÖ®Â±ÄÁä∂ÊÄÅ‰∏≠Ëé∑ÂèñÁî®Êà∑ID
    const userInfo = uni.getStorageSync('userInfo');
    if (userInfo && userInfo.id) {
      this.userId = userInfo.id;
    }
    this.loadDetail(); 
  }
}
</script>

<style>
.container {
  min-height: 100vh;
  background-color: #F8F7F2;
  display: flex;
  flex-direction: column;
  position: relative;
  padding-bottom: 120rpx;
}

/* Âä†ËΩΩÁä∂ÊÄÅ */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
}

.loading-spinner {
  width: 60rpx;
  height: 60rpx;
  border: 6rpx solid #f3f3f3;
  border-top: 6rpx solid #8A3324;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
  margin-bottom: 20rpx;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-text {
  font-size: 28rpx;
  color: #666;
  font-family: "FangSong", serif;
}

/* Á©∫Áä∂ÊÄÅ */
.empty-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
}

.empty-icon {
  width: 200rpx;
  height: 200rpx;
  margin-bottom: 30rpx;
}

.empty-title {
  font-size: 32rpx;
  font-weight: bold;
  color: #333;
  margin-bottom: 10rpx;
  font-family: "FangSong", serif;
}

.empty-desc {
  font-size: 28rpx;
  color: #666;
  margin-bottom: 40rpx;
